<template>
  <div id="wrap">
    <main class="main">
      <section class="columns">
        <div class="container">
          <div class="row options">
            <div class="col-12 butttons" style="max-width: 400px;margin: 30px auto;font-size: 19px;">
              <div class="col-6">
                <a :class="{ 'btn-active' : !isShowFiat}" @click="showCoin()">Coin Balance</a>
              </div>
              <div class="col-6">
                <a @click="showFiat()" :class="{ 'btn-active' : isShowFiat}" >Fiat Balance</a>
              </div>

            </div>
          </div>
          <div class="row options">
            <div class="col-6 no-padding">
              <div class="prepend col-6">
                <img src="./assets/template/assets/imgs/lupa.png" alt="Search">
                <input type="text" v-on:keyup="search()" v-model="query.name">
              </div>
            </div>

            <div class="col-6" style="
    float: right;
">
              <p>Estimated Value: <span>0.00000000 BTC / $0.00</span></p>
              <p>24h Withdrawal Limit: 2 BTC  |  In Use: 0 BTC</p>
            </div>
          </div>
          <div class="row">
            <div class="wrapper">
              <div class="col-12" style="padding-top: 20px">
                <div class="body table mCustomScrollbar" style="max-height: 760px">
                  <table v-if="!isShowFiat">
                    <thead>
                    <tr>
                      <th>Coin
                        <svg xmlns="http://www.w3.org/2000/svg" style="width: 5px;"
                             viewBox="0 0 256 512">
                          <path fill="currentColor"
                                d="M88 166.059V468c0 6.627 5.373 12 12 12h56c6.627 0 12-5.373 12-12V166.059h46.059c21.382 0 32.09-25.851 16.971-40.971l-86.059-86.059c-9.373-9.373-24.569-9.373-33.941 0l-86.059 86.059c-15.119 15.119-4.411 40.971 16.971 40.971H88z"
                                class=""></path>
                        </svg>
                      </th>
                      <th>Name</th>
                      <th>Total Balance</th>
                      <th>Available Balance</th>
                      <th>In Order</th>
                      <th>BTC Value</th>
                      <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(coin,index) in coinBalances" :key="index">
                      <td class="mediumGrid">
                        <span class="show-mobile">Coin</span>
                        <div class="flex">
                          <img :src="require(`./assets/template/assets/imgs/icons/${coin.icon}`)"/>
                          {{coin.coin}}
                        </div>
                      </td>
                      <td class="mediumGrid"><span class="show-mobile" style="margin-bottom: 18px;">
                        Name</span>
                        {{coin.name}}
                      </td>
                      <td>
                        <span class="show-mobile">Total Balance</span>
                        {{coin.balance}}
                      </td>
                      <td><span class="show-mobile">Available Balance</span>{{coin.available_balance}}</td>
                      <td><span class="show-mobile">In Order</span>{{coin.in_order}}</td>
                      <td><span class="show-mobile">BTC Value</span>{{coin.value}}</td>
                      <td style="text-align: right; overflow: hidden">
                        <a href="javascript:void(0)" class="btn btn-action">Deposit</a>
                        <a href="javascript:void(0)" class="btn btn-action">Withdraw</a>
                        <a href="javascript:void(0)" class="btn btn-action">Trade</a>
                      </td>
                    </tr>
                    </tbody>
                  </table>

                  <table v-else>
                    <thead>
                    <tr>
                      <th>Fiat
                        <svg xmlns="http://www.w3.org/2000/svg" style="width: 5px;"
                             viewBox="0 0 256 512">
                          <path fill="currentColor"
                                d="M88 166.059V468c0 6.627 5.373 12 12 12h56c6.627 0 12-5.373 12-12V166.059h46.059c21.382 0 32.09-25.851 16.971-40.971l-86.059-86.059c-9.373-9.373-24.569-9.373-33.941 0l-86.059 86.059c-15.119 15.119-4.411 40.971 16.971 40.971H88z"
                                class=""></path>
                        </svg>
                      </th>
                      <th>Name</th>
                      <th>Total Balance</th>
                      <th>Available Balance</th>
                      <th>In Order</th>
                      <th>BTC Value</th>
                      <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="(fiat,index) in fiatBalances" :key="index">
                      <td class="mediumGrid">
                        <span class="show-mobile">Coin</span>
                        <div class="flex">
                          <img :src="require(`./assets/template/assets/imgs/icons/${fiat.icon}`)"/>
                          {{fiat.fiat}}
                        </div>
                      </td>
                      <td class="mediumGrid"><span class="show-mobile" style="margin-bottom: 18px;">
                        Name</span>
                        {{fiat.name}}
                      </td>
                      <td>
                        <span class="show-mobile">Total Balance</span>
                        {{fiat.balance}}
                      </td>
                      <td><span class="show-mobile">Available Balance</span>{{fiat.available_balance}}</td>
                      <td><span class="show-mobile">In Order</span>{{fiat.in_order}}</td>
                      <td><span class="show-mobile">BTC Value</span>{{fiat.value}}</td>
                      <td style="text-align: right; overflow: hidden">
                        <a href="javascript:void(0)" class="btn btn-action">Deposit</a>
                        <a href="javascript:void(0)" class="btn btn-action">Withdraw</a>
                        <a href="javascript:void(0)" class="btn btn-action">Trade</a>
                      </td>
                    </tr>
                    </tbody>
                  </table>
                </div>
              </div>
            </div>
          </div>
          <div class="row mt-5 row-pagination">
            <sliding-pagination
              :current="query.currentPage"
              :total="query.totalPages"
              @page-change="pageChangeHandler"
            ></sliding-pagination>
          </div>
        </div>
      </section>
    </main>
  </div>
</template>

<script>
import SlidingPagination from 'vue-sliding-pagination'
import { mapGetters } from 'vuex'
export default {
  name: 'App',
  components: { SlidingPagination },
  data () {
    return {
      isShowFiat: false,
      query: {
        name: '',
        currentPage: 1,
        totalPages: 10
      }
    }
  },
  created () {
    this.fetchCoin()
    this.fetchFiat()
  },
  computed: {
    ...mapGetters({
      'coinBalances': 'coinBalances',
      'fiatBalances': 'fiatBalances'
    }),
    filteredContBySearch () {
      return this.coinBalances(this.search.toLowerCase())
    }
  },
  methods: {
    fetchCoin () {
      this.$store.dispatch('fetchCoinBalance', this.query)
    },
    fetchFiat () {
      this.$store.dispatch('fetchFiatBalance', this.query)
    },
    pageChangeHandler (selectedPage) {
      this.query.currentPage = selectedPage
      if (this.isShowFiat) {
        this.fetchFiat()
      } else {
        this.fetchCoin()
      }
    },
    showFiat () {
      this.isShowFiat = true
    },
    showCoin () {
      this.isShowFiat = false
    },
    search () {
      if (this.isShowFiat) {
        this.fetchFiat()
      } else {
        this.fetchCoin()
      }
    }
  }
}
</script>

<style>
  @import "./style.css";
  @import "./balance.css";
  @import "./pagination.css";
</style>



