import Vue from 'vue'
import Vuex from 'vuex'
// eslint-disable-next-line standard/object-curly-even-spacing
import {fetchCoin, fetchFiat } from './api/Balance'

const FETCH_COIN_BALANCES_SUCCESS = 'FETCH_COIN_BALANCES_SUCCESS'
const FETCH_FIAT_BALANCES_SUCCESS = 'FETCH_FIAT_BALANCES_SUCCESS'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    coin_balances: [],
    fiat_balances: []
  },
  getters: {
    coinBalances: state => {
      return state.coin_balances
    },
    fiatBalances: state => {
      return state.fiat_balances
    }
  },
  mutations: {
    [FETCH_COIN_BALANCES_SUCCESS] (state, data) {
      state.coin_balances = data
    },
    [FETCH_FIAT_BALANCES_SUCCESS] (state, data) {
      state.fiat_balances = data
    }
  },
  actions: {
    fetchCoinBalance ({commit}, payload) {
      const result = fetchCoin(payload)
      commit(FETCH_COIN_BALANCES_SUCCESS, result)
    },
    fetchFiatBalance ({commit}, payload) {
      const result = fetchFiat(payload)
      commit(FETCH_FIAT_BALANCES_SUCCESS, result)
    }
  }
})

export default store



